import React, { useEffect, useState, useRef } from 'react';
import {
  Box, Typography, Card, CardMedia, CardContent, Grid, Container, CircularProgress,
  Drawer, List, ListItem, ListItemButton, ListItemText, Toolbar, AppBar, CssBaseline, Divider
} from '@mui/material';

const drawerWidth = 280;

function EmptyState() {
  return (
    <Box display="flex" flexDirection="column" alignItems="center" justifyContent="center" minHeight="40vh">
      <img src="https://cdn-icons-png.flaticon.com/512/4076/4076549.png" alt="Nenhuma imagem" width={120} style={{ opacity: 0.3, marginBottom: 16 }} />
      <Typography variant="h5" color="text.secondary">Nenhuma imagem criada</Typography>
    </Box>
  );
}

function ImageCard({ url, filename, date }) {
  return (
    <Card sx={{ width: 220, m: 1, boxShadow: 2, cursor: 'pointer', transition: '0.2s', '&:hover': { boxShadow: 6 } }} onClick={() => window.open(url, '_blank')}>
      <CardMedia component="img" height="140" image={url} alt={filename} />
      <CardContent sx={{ p: 1 }}>
        <Typography variant="body2" color="text.secondary" sx={{ wordBreak: 'break-all' }}>{filename}</Typography>
        <Typography variant="caption" color="text.disabled">{date}</Typography>
      </CardContent>
    </Card>
  );
}

function App() {
  const [groups, setGroups] = useState(null);
  const [loading, setLoading] = useState(true);
  const [selectedSession, setSelectedSession] = useState(null);
  const wsRef = useRef(null);

  const fetchImages = async () => {
    setLoading(true);
    const res = await fetch('/api/images');
    const data = await res.json();
    setGroups(data);
    setLoading(false);
    // Seleciona a primeira sessão automaticamente se não houver seleção
    if (data && Object.keys(data).length > 0 && !selectedSession) {
      setSelectedSession(Object.keys(data)[0]);
    }
    // Se a sessão selecionada sumiu, seleciona outra
    if (selectedSession && data && !data[selectedSession]) {
      setSelectedSession(Object.keys(data)[0] || null);
    }
  };

  useEffect(() => {
    fetchImages();
    // WebSocket para atualização instantânea
    const wsProtocol = window.location.protocol === 'https:' ? 'wss' : 'ws';
    const ws = new window.WebSocket(wsProtocol + '://' + window.location.host);
    wsRef.current = ws;
    ws.onmessage = (event) => {
      if (event.data === 'new-image') {
        fetchImages();
      }
    };
    return () => ws.close();
    // eslint-disable-next-line
  }, []);

  const sessionIds = groups ? Object.keys(groups) : [];
  const images = selectedSession && groups ? groups[selectedSession] : [];

  return (
    <Box sx={{ display: 'flex', bgcolor: '#f5f5f5', minHeight: '100vh' }}>
      <CssBaseline />
      <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1, bgcolor: '#222' }}>
        <Toolbar>
          <Typography variant="h5" noWrap component="div">
            Admin - Imagens Heatmap
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          [`& .MuiDrawer-paper`]: { width: drawerWidth, boxSizing: 'border-box', bgcolor: '#23272f', color: '#fff' },
        }}
      >
        <Toolbar />
        <Box sx={{ overflow: 'auto', pt: 2 }}>
          <Typography variant="subtitle1" sx={{ px: 2, mb: 1, color: '#bbb' }}>Sessões Ativas</Typography>
          <Divider sx={{ bgcolor: '#444' }} />
          <List>
            {sessionIds.length === 0 && (
              <ListItem>
                <ListItemText primary={<span style={{ color: '#888' }}>Nenhuma sessão</span>} />
              </ListItem>
            )}
            {sessionIds.map((sessionId) => (
              <ListItem key={sessionId} disablePadding>
                <ListItemButton
                  selected={selectedSession === sessionId}
                  onClick={() => setSelectedSession(sessionId)}
                  sx={{ borderRadius: 1, mx: 1, my: 0.5 }}
                >
                  <ListItemText
                    primary={<span style={{ fontWeight: 500, color: selectedSession === sessionId ? '#90caf9' : '#fff' }}>{sessionId}</span>}
                    secondary={groups[sessionId].length + ' imagens'}
                    secondaryTypographyProps={{ color: '#bbb' }}
                  />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        </Box>
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}>
        <Toolbar />
        <Container maxWidth="lg">
          {loading ? (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="40vh">
              <CircularProgress />
            </Box>
          ) : !selectedSession || images.length === 0 ? (
            <EmptyState />
          ) : (
            <>
              <Typography variant="h6" color="primary" sx={{ mb: 2 }}>Sessão: {selectedSession}</Typography>
              <Grid container spacing={2}>
                {images.map(file => {
                  const url = `/uploads/${file}`;
                  const match = file.match(/_(\d+)\./);
                  const date = match ? new Date(Number(match[1])).toLocaleString() : '';
                  return (
                    <Grid item key={file}>
                      <ImageCard url={url} filename={file} date={date} />
                    </Grid>
                  );
                })}
              </Grid>
            </>
          )}
        </Container>
      </Box>
    </Box>
  );
}

export default App;
